.errorInput {
    margin: 10px;
    background: #003300;
    color: #99ff99;
    border: 2px solid red;
    outline: none;
}

.button {
    margin: 10px;
    width: 60px;
    background: #003300;
    color: #99ff99;
    outline: none;
}

button:focus {
    outline: #99ff99 solid 1px;
}

.button:active {
    background: #99ff99;
}

.button:disabled {
    color: #005500;
}

.count {
    margin: 10px;
}